import appAd from "../../../components/page-component/app-ad/app-ad"
import {mapState} from "vuex";

export default {
    data() {
        return {
            unit_id: '',
            list: [],
            ad: false,
            space: true,
            daily_real_num: 0,
            page: 2,
            join: false,
            isSubmit: false,
            activity_id: '',
            open_date: '',
            step_num: 0,
            over: false,
            authorize: false,
            ad_data: {},
            setting: null,
            success: false,
            adHeight: 0,
            coupon_url: this.$api.step.receive,
        }
    },
    components: {
        appAd
    },
    computed: {
        ...mapState({
            stepImg: state => state.mallConfig.plugin.step,
            userInfo: state => state.user.info,
        })
    },
    filters: {
        getObje(data, temp) {
            return {
                id: data.id
            }
        }
    },
    methods: {
        getHeight() {
            const self = this;
            setTimeout(() => {
                const query = uni.createSelectorQuery().in(this);
                query.select('#ad').boundingClientRect(res => {
                    self.adHeight = res.height;
                }).exec();
            })
        },
        toSubmit(step_num, activity_id) {
            this.step_num = step_num;
            this.activity_id = activity_id;
            this.isSubmit = !this.isSubmit;
        },

        toLog() {
            uni.navigateTo({url: '/plugins/step/log/log'});
        },

        toRules() {
            // uni.navigateTo({url: '/plugins/step/rules/rules?rule=2'});
            uni.navigateTo({
                url: `/pages/rules/index?url=${encodeURIComponent(this.$api.step.setting)}&key=activity_rule`,
            });
        },

        toJoin(id) {
            uni.navigateTo({url: '/plugins/step/join/join?id=' + id});
        },

        // 提交成绩
        submit() {
            let that = this;
            let code, iv, encrypted_data;
            let num = that.daily_real_num;
            let activity_id = that.activity_id;
            let step_num = that.step_num;
            that.isSubmit = false;
            if (num < step_num) {
                uni.showToast({
                    title: '成绩未达标',
                    icon: 'none',
                    duration: 1000
                });
            } else {
                uni.showLoading({
                    mask: true,
                    title: '提交成绩中...'
                });

                uni.login({
                    success(res) {
                        code = res.code;
                        // 获取iv和encryptedData
                        uni.getWeRunData({
                            success(res) {
                                iv = res.iv;
                                encrypted_data = res.encryptedData;
                                that.$request({
                                    url: that.$api.step.activity_submit,
                                    method: 'POST',
                                    data: {
                                        encrypted_data: encrypted_data,
                                        iv: iv,
                                        code: code,
                                        activity_id: activity_id,
                                        num: num
                                    },
                                }).then(response => {
                                    uni.hideLoading();
                                    if (response.code == 0) {
                                        that.success = true;
                                    } else {
                                        uni.showToast({
                                            title: response.msg,
                                            icon: 'none',
                                            duration: 1000
                                        });
                                    }
                                }).catch(response  => {
                                    uni.hideLoading();
                                    uni.showToast({
                                        title: response,
                                        icon: 'none',
                                        duration: 1000
                                    });
                                });
                            }
                        })
                    }
                })























































            }
        },
        getSetting() {
            let that = this;
            that.$request({
                url: that.$api.step.setting,
            }).then(response=>{
                if(response.code == 0) {
                    that.setting = response.data;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        auth() {
            let that = this;
            uni.getSetting({
                success(res) {
                    if (res.authSetting['scope.werun'] == true) {
                        that.page = 1;
                        that.getList();
                    } else {
                        uni.authorize({
                            scope: 'scope.werun',
                            success(res) {
                                if (res.errMsg == "authorize:ok") {
                                    // 读取数据
                                    that.page = 1;
                                    that.getList();
                                }
                            },
                            fail(res) {
                                that.$hideLoading();
                                uni.showModal({
                                    title: '提示',
                                    content: '获取步数信息失败，需要授权获取步数权限',
                                    showCancel: false,
                                    confirmText: '打开授权',
                                    success(e) {
                                        if (e.confirm) {
                                            uni.openSetting({});
                                        }
                                    }
                                });
                            }
                        });
                    }
                },
                fail(res) {
                    that.authorize = true;
                    that.$hideLoading();
                }
            })
        },
        getList() {
            let that = this;
            let code, iv, encrypted_data;
            uni.login({
                success(res) {
                    code = res.code;

                    uni.getWeRunData({
                        success(res) {
                            iv = res.iv;
                            encrypted_data = res.encryptedData;
                            that.$request({
                                url: that.$api.step.activity,
                                method: 'POST',
                                data: {
                                    encrypted_data: encrypted_data,
                                    iv: iv,
                                    code: code,
                                    page: that.page
                                },
                            }).then(response => {
                                that.$hideLoading();
                                if (response.code == 0) {
                                    that.ad_data = response.list.ad_data;
                                    that.getHeight();
                                    that.daily_real_num = response.list.daily_real_num;
                                    let list = that.list;
                                    let activity_data = response.list.activity_data;
                                    that.page++;
                                    if(activity_data.length > 0) {
                                        if (list.length > 0) {
                                            for (let i = 0; i < list.length; i++) {
                                                if (activity_data[0].id == list[i].id) {
                                                    return
                                                }
                                            }
                                        }
                                        that.more = false;
                                        if (activity_data.length == 3) {
                                            that.more = true;
                                        }
                                        that.list = list.concat(activity_data);
                                    }
                                } else {
                                    uni.showToast({
                                        title: response.msg,
                                        icon: 'none',
                                        duration: 1000
                                    });
                                }
                            }).catch(response => {
                                uni.showToast({
                                    title: response,
                                    icon: 'none',
                                    duration: 1000
                                });
                            });
                        },
                        fail(res) {
                            if (res.errMsg == 'getWeRunData:fail cancel') {
                                uni.showModal({
                                    content: '读取失败，请稍后再试',
                                    showCancel: false,
                                });
                            } else if (res.errMsg == 'getWeRunData: fail device not support') {
                                uni.showModal({
                                    content: '请在微信中搜索"微信运动"公众号，并点击关注',
                                    showCancel: false,
                                });
                            } else {
                                uni.showModal({
                                    content: res.errMsg,
                                    showCancel: false,
                                });
                            }

                        }
                    })







































































                },
                fail(res) {
                    uni.showModal({
                        content: res.errMsg,
                        showCancel: false,
                    });
                }
            })
        },
        closeTip(status) {
            this.success = false;
            this.join = false;
            if(status == 1) {
                this.list = [];
                this.page = 1;
                this.$showLoading({
                    type: 'global',
                    text: '加载中...'
                });
                this.getList();
            }
        },
    },
    onReachBottom() {
        if(this.more) {
            this.getList();
        }
    },

    onShareAppMessage() {
        return this.$shareAppMessage({
            title: '步数挑战',
            path: "/plugins/step/index/index",
            params: {
                user_id: this.userInfo.options.user_id
            }
        });
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.$store.dispatch('user/info');
        if (options.submit == 1) {
            that.activity_id =  options.activity_id;
            that.step_num =  options.step_num;
            that.isSubmit =  true;
        } else if (options.join == 1) {
            that.join = true
            that.open_date = options.date
        }
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.auth();
        that.getSetting();
    }
}
